import { lazy } from "react";
import IntroContent from "../../content/IntroContent.json";
import MissionContent from "../../content/MissionContent.json";
import GamesContent from "../../content/GamesContent.json";
import TeamContent from "../../content/TeamContent.json";

const MissionBlock = lazy(() => import("../../components/MissionBlock"));
const GamesBlock = lazy(() => import("../../components/GamesBlock"));
const Container = lazy(() => import("../../common/Container"));
const ScrollToTop = lazy(() => import("../../common/ScrollToTop"));
const ContentBlock = lazy(() => import("../../components/ContentBlock"));

const Home = () => {
  return (
    <Container>
      <ScrollToTop />
      <ContentBlock
        type="right"
        title={IntroContent.title}
        content={IntroContent.text}
        button={IntroContent.button}
        image="smashBlox.png"
        id="about"
      />
      <GamesBlock
        title={GamesContent.title}
        content={GamesContent.text}
        games={GamesContent.games}
        id="games"
      />
      <MissionBlock
        title={MissionContent.title}
        content={MissionContent.text}
        button={MissionContent.button}
        id="mission"
      />
      <ContentBlock
        type="left"
        title={TeamContent.title}
        content={TeamContent.text}
        section={TeamContent.section}
        image="team.png"
        id="team"
      />
    </Container>
  );
};

export default Home;
